
#game,
canvas {
  width: 100% !important;
  height: 100% !important;
}
@font-face {
  font-family: 'Font1';
  src: url("data:application/octet-stream;base64,");
}
*{
  padding: 0;
  margin: 0;
}
#app, body, html {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  width: 100%;
  height: 100%;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  overflow: hidden;
}
li{
  list-style-type: none;
}